import React from "react"
import PropTypes from 'prop-types';
import { Formik, Form, Field} from 'formik';
import * as Yup from 'yup';


const ContactForm = (props) => {

    let hasIcon = props.spanClass;
    let hasHeaderClass = props.headerClass;

    // const onSubmit = async (values, setSubmitting) => {
    //     console.log(values);
    // }

    return (
        <section>
            {hasIcon ? <span className={props.spanClass}></span> : ""}
            {hasHeaderClass ? <h2 className={props.headerClass}>{props.headerText}</h2> : <h2>{props.headerText}</h2>}
            
            <Formik
                initialValues={{
                    name: "",
                    email: "",
                    subject: "",
                    message: ""
                }}
                validationSchema={Yup.object({
                    name: Yup.string()
                        .max(30, 'Must be 30 characters or less')
                        .required('Name is required'),
                    email: Yup.string()
                        .email('Invalid email address')
                        .required('Email is required'),
                    subject: Yup.string()
                        .max(30, 'Must be 30 characters or less')
                        .required('Subject is required'),
                    message: Yup.string()
                        .max(1024, 'Must be 1024 character or less')
                        .required('Message is required')
                })}

                onSubmit={(values, { setSubmitting }) => {
                    setSubmitting(true);
                    fetch(
                            "contactproc_json.php", {
                            credentials:"same-origin",
                            method:"POST",
                            body:JSON.stringify(values),
                            headers: new Headers({"Content-Type": "application/json" })
                        })
                        .then((resp) => resp.json())
                        .then((data) =>  document.getElementById("contactresult").innerHTML = data.html)
                        .catch((err)=> document.getElementById("contactresult").innerHTML = err) //console.log(err))

                        setTimeout(() => {
                            document.getElementById("contactresult").innerHTML = "";
                            document.getElementById("contactForm").reset();
                            setSubmitting(false);
                          }, 2000);
                  }}
            >

                {({ errors, touched, isSubmitting }) => (
                <Form id="contactForm">
                    {errors.name && touched.name ? (<div className="formerror">{errors.name}</div>) : null}
                    <Field name="name" type="text" placeholder="Name"/><br/>
                    
                    {errors.email && touched.email ? (<div className="formerror">{errors.email}</div>) : null}
                    <Field name="email" type="text" placeholder="Email"/><br/>
                    
                    {errors.subject && touched.subject ? (<div className="formerror">{errors.subject}</div>) : null}
                    <Field name="subject" type="text" placeholder="Subject"/><br/>
                    
                    {errors.message && touched.message ? (<div className="formerror">{errors.message}</div>) : null}
                    <Field name="message" as="textarea" placeholder="Message"/>
                    
                    <ul className="actions">
                      <li><button type="submit" disabled={isSubmitting} className="btn fa-input"> &#xf2b9; Submit</button></li>
                      <li><button type="reset" className="btn fa-input"> &#xf0e2; Clear</button></li>
                    </ul>
                </Form>
                )}
            </Formik>


                <div id="contactresult" style={{textAlign:'center'}}></div>
            
        </section>
    )
}

ContactForm.propTypes = {
    spanClass: PropTypes.string,
    headerClass: PropTypes.string,
    headerText: PropTypes.string
  }

export default ContactForm
