import React from "react"
import PropTypes from 'prop-types';

const Section = (props) => {

  let hasIcon = props.spanClass;
  let hasHeaderClass = props.headerClass;

  return (
    <section>
      {hasIcon ? <span className={props.spanClass}></span> : ""}
      {hasHeaderClass ? <h2 className={props.headerClass}>{props.headerText}</h2> : <h2>{props.headerText}</h2>}
      {props.children}
    </section>
  )
}

Section.propTypes = {
  spanClass: PropTypes.string,
  headerClass: PropTypes.string,
  headerText: PropTypes.string,
  children: PropTypes.oneOfType([PropTypes.object, PropTypes.array])
}

export default Section
