import React from "react"

const FindMe = () => {

    return (
        <section>
            <h2>Find me on...</h2>
            <div>
                <ul className="social">
                    <li><a href="https://twitter.com/seebofunk" className="icon fa-twitter" target="_blank" rel="noopener noreferrer"><span className="label">Twitter</span></a></li>
                    <li><a href="https://www.facebook.com/christopherboehm" className="icon fa-facebook" target="_blank" rel="noopener noreferrer"><span className="label">Facebook</span></a></li>
                    <li><a href="https://www.linkedin.com/in/chris-boehm-36121b42/" className="icon fa-linkedin" target="_blank" rel="noopener noreferrer"><span className="label">LinkedIn</span></a></li>
                </ul>
            </div>
        </section>
    )
}

export default FindMe