import React from "react"
import PropTypes from 'prop-types';

/**
 * props.targetDate should be passed in using format:
 * "month day, year hour:minute:seconds", eg: "Jan 7, 2024 08:15:30"
 */
class Countdown extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            countdown: <img alt="Loading" src={require("../images/ajax-loader.gif")}/>
        };
    }


    //Countdown in 2 second intervals
    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(),
            2000
        );
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {

        // Get todays date and time
        let now = new Date().getTime();

        let value = new Date(this.props.targetDate).getTime();

        // Find the distance between now an the count down date
        let distance = value - now;

        // Time calculations for days, hours, minutes and seconds
        let days = Math.floor(distance / (1000 * 60 * 60 * 24));
        let hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        let minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        let seconds = Math.floor((distance % (1000 * 60)) / 1000);

        let newcount = days + "d " + hours + "h " + minutes + "m " + seconds + "s ";

        if(distance < 0){
            newcount = "That was fun!";
        }

        this.setState({
            countdown: newcount
        });
    }

    render() {
        return (
            this.state.countdown
        );
    }
}

Countdown.propTypes = {
    targetDate: PropTypes.string
}

export default Countdown